/* #region card */
$cardWidth: 200px;
$secondaryColor: #616070;
$buttonColor: #00acdc;
$buttonTextColor: #fff;
$mainColor : #060b35;

.card {
  box-sizing: border-box;
  width: $cardWidth;
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  &__descreption {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $mainColor;
    margin-bottom: 4px;
  }

  &__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $mainColor;
    margin-bottom: 4px;
  }

  &__id {
    color: $secondaryColor;
    margin-bottom: 16px;
  }

  &__rate {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    &-text {
      color: $mainColor;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-content: center;

    margin-bottom: 16px;

    &-text {
      font-size: 12px;
      line-height: 18px;
      color: $secondaryColor;
    }

    &-sum {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: $mainColor;
    }
  }

  &__button {
    display: block;
    height: 40px;
    background-color: $buttonColor;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    font-weight: 700;
    font-size: 14px;
    line-height: 40px;
  }

  &__button:hover {
    background-color: $buttonTextColor;
    border: 1px solid $buttonColor;
    color: $buttonColor;
  }
}
/* #endregion */
